function generateShip(shipLength) {
  const ship = {
    length: shipLength,
    counter: [],
    position: [],
    hit: function (hitCoordinate) {
      if (this.counter.length < shipLength) {
        this.counter.push(hitCoordinate);
      }
      return this.counter.length;
    },
    isSunk: function () {
      if (this.counter.length === shipLength) {
        return true;
      }
      return false;
    },
  };
  return { ship };
}


module.exports = generateShip;
